import React from "react";
import { Navbar } from "../../Modules/navigation";
import { Footer } from "../../Modules/footer";
import launcherImage from "../../../images/stage.png";
import downloadImage from "../../../images/11 1.png";
import mens from "../../../images/image_2024-05-28_16-22-05.png";
import "./main.css";

const StartPage = () => {
  return (
    <div>
      <Navbar />
      <div className="startPage">
        <h1 className="headerStart">КАК НАЧАТЬ ИГРАТЬ? - ВСЕ ПРОСТО!</h1>

        <div className="flexContainer">
          <div className="containerCard__Start">
            <img src={launcherImage} alt="Картинка лаунчера игры" />
            <div className="contextCard">
              <h4>ШАГ: 1</h4>
              <article>Скачай и установи наш официальный лаунчер!</article>
              <p>
                Загрузив setup.exe на свой компьютер запусти его! Выбери
                подходящее место для установки и нажми "установить".
              </p>
              <button className="downloadButton">Загрузите лаунчер</button>
            </div>
          </div>
          <div className="containerCard__Start">
            <div className="contextCard">
              <h4>ШАГ: 2</h4>
              <article>Выбери игровой ник!</article>
              <p>
                Запустив лаунчер, установи игровой ник и выбери место для
                загрузки файлов игры.
                <br />
                <br />
                Имя пользователя обязательно должно быть в формате: Имя_Фамилия
                на английском языке!
                <br />
                <br />
                Пример: Frederick_Galante
              </p>
            </div>
            <img
              className="downloadCase"
              src={downloadImage}
              alt="Картинка лаунчера игры"
            />
          </div>
          <div className="containerCard__Start">
            <img
              className="cardMensImg"
              src={mens}
              alt="Картинка лаунчера игры"
            />
            <div className="contextCard">
              <h4>ШАГ: 3</h4>
              <article>Запускай игру!</article>
              <p>
                Как только игровые файлы будут загружены - нажимай кнопку
                "Играть" и погружайся в удивительный мир Криминальной России
                вместе с <span>Region Project</span>
              </p>
            </div>
          </div>

          <button className="btnStartGame">Начать игру!</button>
        </div>
      </div>
      <Footer />
    </div>
  );
};

export default StartPage;
